<template>
  <div>
    <Card style="margin-bottom:10px;height:300px">
      <p slot="title">
        用户信息
      </p>
      <div style="display:flex">
        <div class="clounm">
          <span>用户ID: {{ user.id }}</span>
          <span>边民姓名: {{ user.realName }}</span>
        </div>
        <!-- <div class="clounm">
          <span>隶属海关: </span>
          <span>互市点: </span>
        </div> -->
        <div class="clounm">
          <span>手机号: {{ user.idCode }}</span>
          <span>身份证号: {{ user.phone }}</span>
        </div>
        <div class="clounm">
          <!-- <span>角色: </span> -->
          <span>否是边民: {{ user.civil ? '是' : '否' }}</span>
        </div>
      </div>
    </Card>
    <Card style="margin-bottom:10px;height:300px">
      <p slot="title">
        边民信息
      </p>
      <div style="display:flex">
        <div class="clounm">
          <span>边民姓名: {{ civilInfo.civilName }}</span>
          <span>备案编号: {{ civilInfo.regNo }}</span>
        </div>
        <!-- <div class="clounm">
          <span>隶属海关: </span>
          <span>互市点: </span>
        </div> -->
        <div class="clounm">
          <span
            >人脸认证状态: {{ civilInfo.faceStatus == 'Y' ? '是' : '否' }}</span
          >
        </div>
      </div>
    </Card>
  </div>
</template>
<script>
import { userDetail } from '@/api/user.js'
import { SERVER_CODE } from '@/utils/constants.js'

export default {
  data() {
    return {
      user: null,
      civilInfo: { civilName: '', regNo: '', faceStatus: '' }
    }
  },
  mounted() {},
  activated() {
    this.getDetail()
  },
  methods: {
    getDetail() {
      let params = {
        id: this.$route.query.userId
      }
      userDetail(params)
        .then(({ code, data, msg }) => {
          if (code === SERVER_CODE.CODE_200) {
            this.user = data
            if (data.civilInfo) {
              //处理civilInfo为null时页面报错
              this.civilInfo = data.civilInfo
            } else {
              this.civilInfo = { civilName: '', regNo: '', faceStatus: '' }
            }
          } else {
            this.$Message.info(msg)
          }
        })
        .catch(error => {
          console.log(error)
        })
        .finally(() => {
          this.loading = false
        })
    }
  }
}
</script>
<style lang="scss" scoped>
.clounm {
  display: flex;
  flex-direction: column;
  margin-right: 100px;

  span {
    display: block;
    margin-bottom: 20px;
  }
}
</style>
